import React, { useState } from 'react';
import '../layouts/researchMethodsForm.css'; // Import CSS file

const ResearchMethodsForm = () => {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({
    researchGoal: '',
    audienceSize: '',
    dataType: '',
    resources: '',
    insightsType: ''
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const nextStep = () => {
    setStep(step + 1);
  };

  const prevStep = () => {
    setStep(step - 1);
  };

  const renderRecommendations = () => {
    const { researchGoal, audienceSize, dataType, resources, insightsType } = formData;

    if (researchGoal === 'Exploratory' && audienceSize === 'Small' && dataType === 'Qualitative') {
      return <p>We recommend conducting **User Interviews** and **Focus Groups**.</p>;
    }

    if (researchGoal === 'Evaluative' && dataType === 'Quantitative') {
      return <p>We recommend **A/B Testing** or **Usability Testing**.</p>;
    }

    return <p>Please review your selections for appropriate suggestions.</p>;
  };

  return (
    <div className="form-container">
      <h1 className="form-title">Choose Your Research Method</h1>

      {step === 1 && (
        <div className="form-step">
          <h2 className="form-question">What is your research goal?</h2>
          <select className="form-select" name="researchGoal" onChange={handleInputChange}>
            <option value="">--Select--</option>
            <option value="Exploratory">Exploratory (Understanding new problems)</option>
            <option value="Evaluative">Evaluative (Assessing existing solutions)</option>
            <option value="Generative">Generative (Creating new ideas)</option>
            <option value="Descriptive">Descriptive (Defining user characteristics)</option>
          </select>
          <button className="form-button" onClick={nextStep}>Next</button>
        </div>
      )}

      {step === 2 && (
        <div className="form-step">
          <h2 className="form-question">What is your audience size?</h2>
          <select className="form-select" name="audienceSize" onChange={handleInputChange}>
            <option value="">--Select--</option>
            <option value="Small">Small (e.g. focus groups)</option>
            <option value="Large">Large (e.g. surveys, experiments)</option>
          </select>
          <button className="form-button" onClick={prevStep}>Back</button>
          <button className="form-button" onClick={nextStep}>Next</button>
        </div>
      )}

      {step === 3 && (
        <div className="form-step">
          <h2 className="form-question">What type of data are you interested in?</h2>
          <select className="form-select" name="dataType" onChange={handleInputChange}>
            <option value="">--Select--</option>
            <option value="Qualitative">Qualitative (Interviews, Focus Groups)</option>
            <option value="Quantitative">Quantitative (Surveys, Metrics)</option>
          </select>
          <button className="form-button" onClick={prevStep}>Back</button>
          <button className="form-button" onClick={nextStep}>Next</button>
        </div>
      )}

      {step === 4 && (
        <div className="form-step">
          <h2 className="form-question">How much time/resources do you have?</h2>
          <select className="form-select" name="resources" onChange={handleInputChange}>
            <option value="">--Select--</option>
            <option value="Short-term">Short-term (Rapid testing)</option>
            <option value="Long-term">Long-term (In-depth research)</option>
          </select>
          <button className="form-button" onClick={prevStep}>Back</button>
          <button className="form-button" onClick={nextStep}>Next</button>
        </div>
      )}

      {step === 5 && (
        <div className="form-step">
          <h2 className="form-question">What type of insights are you looking for?</h2>
          <select className="form-select" name="insightsType" onChange={handleInputChange}>
            <option value="">--Select--</option>
            <option value="Behavioral">Behavioral (User behaviors)</option>
            <option value="Attitudinal">Attitudinal (User opinions)</option>
            <option value="Both">Both</option>
          </select>
          <button className="form-button" onClick={prevStep}>Back</button>
          <button className="form-button" onClick={nextStep}>See Recommendations</button>
        </div>
      )}

      {step === 6 && (
        <div className="form-step">
          <h2 className="form-question">Recommended Research Methods</h2>
          {renderRecommendations()}
          <button className="form-button" onClick={prevStep}>Back</button>
        </div>
      )}
    </div>
  );
};

export default ResearchMethodsForm;
